* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  background-color: #fcb900;
}

.container-sm {
  background-color: white;
  min-height: 600px;
  min-width: 900px;
  width: auto;
  max-height: 70vh;
  overflow-y: auto; /* Add vertical scrollbar if needed */
  padding: 20px;
  border-radius: 3%;
}

.container {
  width: 100%;
  max-height: 100%;
  display: flex;
  justify-content: center;
  background-color: brown;
  align-items: center;
}

.app-wrapper {
  /* background-color: grey; */
  max-width: 80%;
  padding: 150px;
  height: 800px;
  box-sizing: border-box;
  border-radius: 5%;
  box-shadow: ivory;
  margin-top: 2%;
  margin-bottom: 2%;
}

.dropdown-wrapper {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 15%;
}

.btn-show-all {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 15%;
}

.logo-elecctro {
  position: absolute;
  top: 20px;
  left: 40%;
  width: 15%;
  max-width: 100%; /* Ensure the image stays within its container */
  height: auto;
  margin-bottom: 20px;
}

header h1 {
  color: white;
  text-align: center;
}

.task-input {
  outline: none;
  width: 290px;
  padding: 15px;
  margin-right: 25px;
  font-size: 20px;
  color: white;
  background-color: black;
  border-radius: 10px;
}

.form-control {
  width: 500%;
  margin-right: 1%;
}
.title {
  position: absolute;
  top: 20px;
  justify-content: center;
  align-items: center;
}

.todo-btn {
  width: 10%;
  border-radius: 5%;
}

.input-group{
    margin-top: 3%;
}
.btn-show-all {
  width: 10%;
  margin-bottom: 10px;
}

.list-item {
  display: flex;
  margin: 20px 0;
  border: 1px solid white;
  border-radius: 10px;
  padding: 10px;
  max-height: 30px;
  background-color: transparent;
}

.button-complete,
.button-delete,
.button-edit {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;
}

.button-complete {
  color: green;
  margin-right: 10px;
}



.complete {
  text-decoration-style: solid;
  text-decoration-line: line-through;
  text-decoration-color: red;
}

.list {
  width: 220px;
  background-color: transparent;
  border: none;
  color: white;
  font-size: 20px;
  padding-left: 10px;
  margin-right: 15px;
}

input:focus,
button :focus {
  outline: none;
}

.title {
  text-align: center;
}

.logout {
  margin-right: 5px;
}

/* Transition CSS */
.todo-item-enter {
  opacity: 0;
}

.todo-item-enter-active {
  opacity: 1;
  transition: opacity 300ms;
}

.todo-item-exit {
  opacity: 1;
}

.todo-item-exit-active {
  opacity: 0;
  transition: opacity 300ms;
}

/* End of Transition CSS */

/* Media query for smaller screens */
@media screen and (max-width: 768px) {
  .container-sm {
    min-width: unset; /* Remove minimum width for smaller screens */
    padding: 0 10px; /* Optional: Add some padding to the container */
  }
}
